<template>
  <v-container
    id="user-profile"
    fluid
    tag="section"
  >
    <v-row justify="center">
      <v-col
        cols="12"
        md="8"
      >
        <base-material-card
          color="primary"
          icon="mdi-account"
          :title="this.$t('nav.profile')"
          class="px-5 py-3"
        >
          <v-form>
            <v-container class="py-0">
              <v-row>
                <v-col cols="12">
                  <v-row
                    align="center"
                    dense
                  >
                    <v-col cols="10">
                      <v-text-field
                        id="xfo_token"
                        v-model="api.xfo_token"
                        label="Fuel Office token"
                        class="purple-input"
                        :readonly="true"
                      />
                    </v-col>

                    <v-col cols="1">
                      <v-btn
                        color="primary"
                        @click="copyClipboard"
                      >
                        <v-icon>
                          mdi-content-copy
                        </v-icon>
                      </v-btn>
                    </v-col>

                    <v-col
                      v-if="$store.state.admin"
                      cols="1"
                    >
                      <v-btn
                        color="primary"
                        @click="resetToken"
                      >
                        <v-icon>
                          mdi-autorenew
                        </v-icon>
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </base-material-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    data: () => ({
      api: {
        xfo_token: '',
      },
    }),
    mounted: function () {
      this.$http.get(`/portal/user/organization`)
        .then(
          (response) => {
            this.api = response.data
          }
        )
    },
    methods: {
      copyClipboard () {
        document.querySelector('#xfo_token').select()

        try {
          document.execCommand('copy')
          this.$toasted.global.success('Copied')
        } catch (err) {
          this.$toasted.global.error('Oops, unable to copy')
        }
      },
      resetToken () {
        if (!confirm('Are you sure you want to reset the token?')) {
          return
        }

        this.$http.post(
          `/portal/organization/xfo/token`,
          JSON.stringify({
            xfo_token: this.api.xfo_token,
          })
        )
          .then((response) => {
            this.$toasted.global.success(response.data.message)
            this.api.xfo_token = response.data.token
          })
          .catch((error) => {
            this.$toasted.global.error(error.response.data.message)
          })
      },
    },
  }
</script>
